import {
  ApolloClient,
  createHttpLink,
  InMemoryCache,
  ApolloLink,
} from '@apollo/client';
import { useAuthToken } from './components/AuthToken';

// Middleware sem setur a칧gangslykilinn ef hann er til sta칧ar sem authorization header me칧 hverri fyrirspurn e.request 
// sem vi칧 sendum 치 serverinn 칤 gegnum Apollo client.
const authMiddleware = (token) => new ApolloLink((operation, forward) => {
  if (token) {
    operation.setContext({
      headers: {
        authorization: `Bearer ${token}`
      }
    });
  }
  return forward(operation);
});

export const useApolloClient = () => {
  // N치um 칤 a칧gangslykilinn sem er geymdur 칤 vafrak칬ku
  const [token] = useAuthToken();

  let uri;
  if (process.env.NODE_ENV === "production") {
    uri = '/graphql';
  } else {
    uri = 'http://localhost:4000/graphql'
  }
  const httpLink = createHttpLink({
    uri: uri
  });

  const link = authMiddleware(token).concat(httpLink)

  return new ApolloClient({
    link,
    cache: new InMemoryCache({
      /*
        룟rfum a칧 skilgreina "typePolicie" fyrir feed fyrirspurnina
        쭀칤 vi칧 erum a칧 칰tf칝ra pagination fyrir linkana sem vi칧 f치um fr치 serverinum.
        .e. vi칧 쬿rfum a칧 skilgreina hvernig vi칧 geymum linkana okkar 칤 cache-inu 칤 clientinum 
        sj치 docs: https://www.apollographql.com/docs/react/pagination/core-api/
      
      typePolicies: {
        Query: {
          fields: {
            FeedQuery: offsetLimitPagination()
          },
        },
      },
*/
    })

  });

};
