import { LINKS_PER_PAGE } from "./constants";

// Fall sem s칠r um a칧 r칠ttar breytur fylgi sql fyrirspurninni 칰t fr치 쭀칤
// hvort vi칧 erum 치 s칤칧unnni sem s칳nir okkur n칳justu linka (/new/:page)
// e칧a top 10 vins칝lustu linkana (/top)
export const getQueryVariables = (page, type) => {
  const skip = (page - 1) * LINKS_PER_PAGE;
  const take = LINKS_PER_PAGE;
  const orderBy = { createdAt: 'desc' };
  if (page) return { type, take, skip, orderBy };
  return {}
};

// Fall sem les cache-a칧a fyrirspurn af linkum, uppf칝rir atkv칝칧in
// fyrir linkana og skrifar uppf칝r칧a fyrirspurn aftur 칤 cache-i칧
export const readLinksAndUpdateQuery = (query, cache, link, vote) => {
  if (!cache.readQuery(query)) return; // Er fyrirspurnin til 칤 cache-inu?
  const { feed } = cache.readQuery(query);
  const updatedLinks = feed.links.map((feedLink) => {
    if (feedLink.id === link.id) {
      return {
        ...feedLink,
        votes: [...feedLink.votes, vote]
      };
    }
    return feedLink;
  });
  const updatedQuery = {
    ...query,
    data: {
      feed: {
        links: updatedLinks
      }
    },
  }
  cache.writeQuery(updatedQuery);
};







