import { useApolloClient } from '@apollo/client';
import { useCookies } from 'react-cookie';

// Breyta sem heldur utan um nafni칧 치 k칬kunni sem geymir jwt lykilinn
const TOKEN_NAME = 'auth-token';

// B칰um til okkar eigin 'kr칩k' (react-hook) til 쬰ss a칧 s칝kja, vista og ey칧a a칧gangs-lyklinum
// A칧skiljum a칧gangskerfi칧 fr치 k칬kugeymslunni.
export const useAuthToken = () => {
  // Notum react-cookies pakkann sj치: https://www.npmjs.com/package/react-cookie
  const [cookies, setCookie, removeCookie] = useCookies([TOKEN_NAME]);

  // Fall sem vistar a칧gangslykilinn me칧 k칬kunafninu 'auth-token'
  // Setjum flaggi칧 SameSite: strict til 쬰ss a칧 koma 칤 veg fyrir CSRF 치r치sir
  // https://www.owasp.org/index.php/Cross-Site_Request_Forgery_(CSRF)
  // Getum einnig b칝tt vi칧 secure: true .a. a칧eins er h칝gt a칧 
  // nota k칬kuna 치 칬ruggari tengingu -> https (virkar 쮂 ekki 칤 r칩unarumhverfi)
  const setAuthToken = (token) => setCookie(TOKEN_NAME, token, { SameSite: 'strict' });

  // Fjarl칝gjum auth-token lykilinn 칰r k칬kunum, t.d. 쬰gar vi칧 loggum notanda 칰t
  const removeAuthToken = () => removeCookie(TOKEN_NAME);

  return [cookies[TOKEN_NAME], setAuthToken, removeAuthToken];
};

export const useLogout = () => {
  const [, , removeAuthToken] = useAuthToken();

  const apolloClient = useApolloClient(); // apolloClient v칤sar n칰 칤 'client-inn' sem vi칧 bjuggum til 칤 upphafi 칤 App.js

  const logout = async () => {
    removeAuthToken();
    await apolloClient.resetStore();
  };

  return logout;
}